/*
=================
       Body
=================
*/

body {
  overflow: hidden;
}
header {
  top: 0%;
  margin-bottom: -150px;
}
i {
  margin: 1000px 0px 0px 275px;
}
#bordr {
  padding: 100px auto; 
}
/*
=================
       Font
=================
*/

h1 {
  margin-top: -1px;
  font-family: 'Shrikhand';
  color: #000000;
  text-align: center;
  font-size: 5.7vw;
  /*padding-top: 50px;
  margin-bottom: -10px;*/
}
h2 {
  padding-top: 10px;
  text-decoration: underline;
}
h4 {
  font-family: 'Helvetica';
  text-align: center;
  color: #FFFFFF;
  font-size: 1.1rem;
  padding-left: 30px;
  padding-right: 30px;
}
p {
  font-family: 'Helvetica';
  text-align: center;
  color: #FFFFFF;
  font-size: 1.25vw;
  margin-left: 100px;
  padding: 0px 40px 0px 40px;
  margin-right: 100px;
}
